@import url(https://fonts.googleapis.com/css2?family=Saira&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  max-width: 1200px;
  margin: 0 auto;
  flex-grow: 1;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.cContainer {
  /* border: solid 1px black; */
  /* margin-top: 10%; */
}
html {
  background: url(/lianelartiles/static/media/hive.42925bb0.svg),
    linear-gradient(
      180deg,
      rgba(255, 255, 255, 0.3) 0%,
      rgba(255, 255, 255, 0) 100%
    ),
    #292a2e;
  width: 100%;
  background-size: 100% 100%, 100% 100%;
  background-repeat: no-repeat;
  min-height: 100vh;
}
body {
  padding: 0;
  margin: 0 auto;
  font-family: "Saira", sans-serif;
  font-size: 1.2rem;
  color: white;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.link {
  display: flex;
  color: inherit;
  text-decoration: none;
  padding-top: 5px;
  padding-bottom: 5px;
}
@media (orientation: portrait) {
  html {
    background: url(/lianelartiles/static/media/hive-portrait.7599f26f.svg),
      linear-gradient(
        180deg,
        rgba(255, 255, 255, 0.3) 0%,
        rgba(255, 255, 255, 0) 100%
      ),
      #292a2e;
    width: 100%;
    background-size: 100% 100%, 100% 100%;
    background-repeat: no-repeat;
    min-height: 100%;
  }
}

#me {
  text-align: right;
  margin-right: 50%;
  margin-top: 25vh;
}
#las {
  font-size: 56px;
}
#dev {
  font-size: 20px;
}
#lema {
  font-size: 14px;
}
@media (max-width: 1200px) {
  #me {
    text-align: right;
    margin-right: 50vw;
    margin-top: 25vh;
  }
}
@media (max-width: 800px) {
  #me {
    margin-top: 25vh;
    margin-right: 0%;
    text-align: center;
  }
}
@media (max-width: 500px) {
  #me {
    margin-top: 30%;
    margin-bottom: 10px;
    width: 100vw;
  }
}
@media (max-width: 370px) {
  #me {
    margin: auto;
    margin-top: 15%;
    text-align: center;
  }
}
@media (max-height: 450px) {
  #me {
    margin: auto;
    margin-top: 0%;
    text-align: center;
  }
}

.logo {
  display: flex;
  align-items: center;
  color: inherit;
  position: relative;
  width: 100px;
  height: 100px;
  -webkit-filter: drop-shadow(7px 7px 7px rgba(0, 0, 0, 1));
          filter: drop-shadow(7px 7px 7px rgba(0, 0, 0, 1));
}
.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: auto;
  padding-left: 5%;
  padding-right: 5%;
  margin-top: 1%;
}
.header ul {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  -webkit-filter: drop-shadow(6px 6px 4px rgba(0, 0, 0, 0.6));
          filter: drop-shadow(6px 6px 4px rgba(0, 0, 0, 0.6));
  /* border: solid 1px black; */
}
.header span {
  padding: 10px;
}

@media screen and (max-width: 800px) {
  .logo {
    margin: auto;
    margin-top: 20%;
  }
  .main-header {
    flex-direction: row;
    margin: auto;
  }
}
@media (max-width: 500px) {
  .logo {
    margin: auto;
    margin-top: 40%;
  }
  .main-header {
    flex-direction: column;
    margin: auto;
  }
}
@media (max-width: 370px) {
  .main-header {
    flex-direction: row;
  }
  .header ul {
    flex-direction: column;
    text-align: center;
  }
  .logo {
    margin: auto;
    margin-top: 15%;
  }
  .header {
    margin-top: 5%;
  }
  .header span {
    padding: 0px;
    margin: 0%;
  }
}

.main-footer {
  color: #292a2e;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background: linear-gradient(
      180deg,
      rgba(255, 255, 255, 0.5) 0%,
      rgba(255, 255, 255, 0) 100%
    ),
    #b8b8b8;
  align-items: center;
}
.footer {
  display: flex;
  list-style: none;
  flex-direction: row;
  -webkit-filter: drop-shadow(3px 3px 4px rgba(0, 0, 0, 0.9));
          filter: drop-shadow(3px 3px 4px rgba(0, 0, 0, 0.9));
}
.footer span {
  padding: 10px;
}
.flags {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.flags img {
  height: 20px;
  width: 30px;
  -webkit-filter: drop-shadow(3px 3px 4px rgba(0, 0, 0, 0.5));
          filter: drop-shadow(3px 3px 4px rgba(0, 0, 0, 0.5));
}

.toggle {
  height: 50px;
  width: 50px;
}
#toggle {
  -webkit-filter: drop-shadow(3px 3px 4px rgba(0, 0, 0, 0.5));
          filter: drop-shadow(3px 3px 4px rgba(0, 0, 0, 0.5));
  height: 50px;
  width: 50px;
}

@media (max-width: 800px) {
  .toggle {
    justify-content: space-around;
  }
}
@media (max-width: 500px) {
  .footer {
    flex-direction: column;
  }
  .footer span {
    padding: 0%;
    margin: auto;
  }
}
@media (max-width: 320px) {
  .footer {
    flex-direction: column;
  }
}

